$primary-color: #AD1FEA;
$primary-hover: #C75bf6;
$secondary-color: #4661E6;
$secondary-hover: #7C91f9;
$cancel: #3A4374;
$cancel-hover: #656ea3;
$warning: #d73737;
$warning-hover: #e98888;
$unselected: #F2F4FF;
$selected: #CFD7FF;

$banner: #373f68;
$background: #F7F8FD;

$light-blue: #62BCFA;
$salmon: #F49F85;

$white-font: #FFFFFF;
$dark-font: #3A4374;
$light-font: #647196;

body {
  background-color: $background;
}

.white-font {
  color: $white-font;
}

.dark-font {
  color: $dark-font;
}

.light-font {
  color: $light-font;
}

.button {
  color: $white-font;
  border: none;
  border-radius: 10px;
  font-weight: bold;
  padding: .8rem 1.5rem;
  cursor: pointer;
}

// Primary Button Class
.button-primary {
  background-color: $primary-color;
}

.button-primary:hover {
  background-color: $primary-hover;
}

// Secondary Button Class
.button-secondary {
  background-color: $secondary-color;
}

.button-secondary:hover {
  background-color: $secondary-hover;
}

// Cancel Button Class
.button-cancel {
  background-color: $cancel;
}

.button-cancel:hover {
  background-color: $cancel-hover;
}

// Warning Button Class
.button-warning {
  background-color: $warning;
}

.button-warning:hover {
  background-color: $warning-hover;
}

// Tags & Vote
.tag-vote {
  background-color: $unselected;
  border-radius: 12px;
}

.tag-vote:hover {
  background-color: $selected;
}

.tag {
  font-size: 12px;
  font-weight: 700;
  color: $secondary-color
}

.tagActive {
  font-size: 12px;
  font-weight: 700;
  color: $white-font;
}

.allActive {
  background-color: $secondary-color;
}



// FONTS

h1 {
  font-size: 24px;
  font-weight: bold;
  line-height: 35px;
  letter-spacing: -0.33;
}

h2 {
  font-size: 20px;
  font-weight: bold;
  line-height: 29px;
  letter-spacing: -0.25;
}

h3 {
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  letter-spacing: -0.25;
}

h4 {
  font-size: 14px;
  font-weight: bold;
  line-height: 20px;
  letter-spacing: -0.2;
}

.body-1 {
  line-height: 23px;
}

.body-2 {
  font-size: 15px;
  line-height: 22px;
}

.body-3 {
  font-size: 13px;
  line-height: 19px;
}


// FORM ELEMENTS

.field {
  color: $dark-font;
  background-color: $background;
  border: none;
  border-radius: 4px;
}

// Active
.field-active {
  border: $secondary-color 2px solid;
}

// Error
.field-error {
  border: $warning 2px solid;
}

// DROP DOWN
.nativeSelect,
.customSelect {
  position: relative;
  width: 100%;
  height: 2.5rem;
  font-size: .9em;
}

.customSelect {
  position: absolute;
  top: 62%;
  left: 0;
  display: none;
}

@media (hover: hover) {
  .customSelect {
    display: block;
  }

  .nativeSelect:focus + .customSelect {
    display: none;
  }
}

.customSelectTrigger {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 1.3rem;
  color: $dark-font;
}

.customSelectOptions {
  position: absolute;
  top: 3.5rem;
  left: 0;
  width: 100%;
  border: none;
  border-radius: 0.5rem;
  background-color: #fff;
  box-shadow: 0 10px 30px #c9cacf;
  z-index: 1;
  display: none;
}

.customSelectHidden {
  display: block;
}

.customSelectOptions:hover {
  display: block;
}

.customSelectOption {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 0.8rem 1.5rem;
  color: $light-font
}

.customSelectOption.isHover,
.customSelectOption:hover {
  color: $primary-color;
  cursor: pointer;
}

.customSelectOption:not(:last-of-type)::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #e1e3ea;
}

.customSelectOption.isActive::before {
  content: "✓";
  position: absolute;
  left: 0.8rem;
}

// Default
.drop-down {
  background-color: $background;
  border-radius: 4px;
  cursor: pointer;
}

.drop-down:hover {
  border: 2px solid #5d74e9;
  margin: -2px;
}

// Active
.drop-down-active {
  border: 2px solid #5d74e9 ;
  margin: -2px;
}

// Menu
.drop-down-menu {
  border: none;
  border-radius: 4px;

  filter: drop-shadow(5px 5px 10px #4661E6);

  li {
    color: $light-font;
  }

  li:hover {
    color: $primary-color;
  }
}

// ROADMAP
.circle-decorator {
  height: .6rem;
  width: .6rem;
  margin: .4rem 1rem 0 0;
  border-radius: 50%;
}

.planned {
  background-color: $salmon;
}

.in-progress {
  background-color: $primary-color;
}

.live {
  background-color: $light-blue;
}
